export default {
	data() {
		return {
			hasAddress: false,
			name: '',
			phoneNum: '',
			address: '请填写收货地址',
			detailAddress: '',
			disabled: true,
			checkPhoneNum: false
		};
	},
	methods: {
		// 获得收货人姓名
		getName(e) {
			console.log(e.detail.value)
			if (e.detail.value) {
				this.name = e.detail.value
			}
			this.judge()
		},
		// 获得电话号码
		getPhoneNum(e) {
			if (e.detail.value) {
				this.phoneNum = e.detail.value
			}
			this.judge()
		},
		// 校验手机号格式是否正确
		checkPhone(){
			let reg = /^1[3|4|5|7|8][0-9]{9}$/;
			if(reg.test(this.phoneNum)){
				this.checkPhoneNum = true
			}else{
				this.checkPhoneNum = false
				uni.showToast({
					title: '请输入正确的手机号',
					duration: 1000,
					icon: 'none'
				})
			}
		
			console.log(reg.test(this.phoneNum))
		},
		getDetailAddress(e) {
			if (e.detail.value) {
				this.detailAddress = e.detail.value
			}
			this.judge()
		},
		getAddress() {
			uni.chooseLocation({
				success: (res) => {
					console.log(res)
					this.address = res.address + ' ' + res.name
					this.hasAddress = true
				},
				fail: (error) => {
					console.log(error)
				}
			})
			this.judge()
		},
		judge(){
			if(this.name !== '' && this.phoneNum !== '' && this.address !== '' && this.detailAddress !== ''){
				this.disabled = false
			}else{
				this.disabled = true
			}
		},
		keep() {
			if (this.name !== '' && this.phoneNum !== '' && this.address !== '请填写收货地址' && this.detailAddress !== '') {
				if(this.checkPhoneNum){
					console.log('保存成功')
					this.$api.addressAdd({
						name: this.name,
						phone: this.phoneNum,
						address: this.address,
						house: this.detailAddress,
						is_confirm: 2
					}).then(res => {
						console.log(res)
						if(res.data.errno === 0){
							uni.showToast({
								title: '添加成功',
								duration: 1000,
								icon: 'none'
							})
							// 返回上一级
							uni.navigateBack({
							    delta: 1
							});
						}else{
							uni.showToast({
								title: '添加失败，请重试',
								duration: 1000,
								icon: 'none'
							})
						}
					})
				}else{
					uni.showToast({
						title: '请输入正确的手机号',
						duration: 1000,
						icon: 'none'
					})
				}
				
			} else {
				uni.showToast({
					title: '资料未填写完整',
					duration: 1000,
					icon: 'none'
				})
			}
		}
	}
}
